@import url(https://fonts.googleapis.com/css?family=Heebo);
html,body{box-sizing:border-box;margin:0;padding:0;overflow-x:hidden}.App{width:100vw;height:100vh;max-width:100%;font-family:'Heebo', sans-serif}.App>.page{width:100%}

.CoinList{display:flex;width:100%;height:100%;flex-wrap:wrap;justify-content:center;align-items:center}

.CoinListItem.card{border:1px solid #eee;margin:10px;padding:10px;border-radius:20px;background:#dde4f8;color:#545fb1}.CoinListItem.card>header{display:flex;justify-content:space-between;font-size:1.5em;font-weight:bold}.CoinListItem.card header.full-row{text-align:center;background:#545fb1;margin:0 -10px;padding:5px;color:#EEE}.CoinListItem.card:hover{background:#545fb1;color:#dde4f8;cursor:pointer}.CoinListItem.card:hover header.full-row{background:#dde4f8;color:#545fb1}.CoinListItem.card{width:30%}.CoinListItem.card>.sources{margin-top:20px}.CoinListItem.card>.sources>.source{width:100%;display:flex;justify-content:space-between;margin:10px 0px;font-size:0.8em}.CoinListItem.card>.sources>.source>div{width:33.3%;text-align:center}

.CoinHistory>header{display:flex;justify-content:center;align-items:center}.CoinHistory>header>.CoinListItem{width:100%}.CoinHistory>main.history{display:flex;justify-content:space-between;padding:20px}.CoinHistory>main.history .source-history>header{font-size:1.3em;color:#545fb1;text-align:center;margin-bottom:10px}.CoinHistory>main.history .source-history .record{display:flex;font-size:0.8em;padding:5px}.CoinHistory>main.history .source-history .record:nth-child(odd){background:#eee}.CoinHistory>main.history .source-history .record>div:first-child{margin-right:30px}

